import React, {Component} from 'react';
import {AppBar, Toolbar, Typography, Button, Badge} from '@material-ui/core';
import {NavLink, Link, withRouter} from 'react-router-dom';
import {isLogin, logout} from '../auth/Utils';
import {withStyles} from '@material-ui/core/styles';
// import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import LockIcon from '@material-ui/icons/Lock';
import NotificationsNoneIcon from '@material-ui/icons/NotificationsNone';

import '../style.css';

class Header extends Component {

  constructor(props) {
    super(props)
    this.state = {
      logintrue: false
    }
  }
  useStyles = withStyles((theme) => ({
    root: {
      flexGrow: 1
    },
    menuButton: {
      marginRight: theme.spacing(2)
    },
    title: {
      flexGrow: 1
    }
  }));
  setUserInfo=()=>{
    var userinfo=JSON.parse(localStorage.getItem('userInfo'));      
      this.setState({userinfos:userinfo},()=>{
        var name2char=this.state.userinfos.name;
            this.setState({name2char:name2char.substr(0,2)});
      });

  }
  componentDidMount = () => {
    if(localStorage.getItem('userInfo')){
      this.setUserInfo();
    }
    (isLogin()) && this.setState({logintrue: true});
    const getHeader = document.getElementsByTagName('header');
    window.onscroll = () => {
      if (window.location.pathname === '/') {
        (window.scrollY > 100) && getHeader[0]
          .classList
          .add('stickbottom');
        (window.scrollY < 100) && getHeader[0]
          .classList
          .remove('stickbottom');
      }
    }
  }

  // logout from the application
  logout = () => {

    this.setState({
      logintrue: false
    }, () => {
      logout();
      this
        .props
        .history
        .push('/');
    })

  }
  render() {
    const classes = this.useStyles;
    return (
      <div className={classes.root}>
        {(this.state.logintrue && this.state.userinfos.role==='customer')
          ? <AppBar position="fixed" color="primary">
              <Toolbar>
                <Typography variant="h6" className={classes.title}>
                  <Link to="/dashboard"><img alt="default_img_react.jpg" src='/assets/logo.png' width="120"/></Link>
                </Typography>

                <div className="right-nav">                 
                  <NavLink to="/" exact>Bookings</NavLink>
                  <NavLink to="/">Orders
                  </NavLink>
                  <NavLink to="/">
                    <Badge color="secondary" variant="dot">
                      <NotificationsNoneIcon/>
                    </Badge>
                  </NavLink>
                  <div className="dropdown">
                    <span
                      className="dropdown-toggle text-uppercase account-dropdown"
                      id="dropdownMenuButton"
                      data-toggle="dropdown"
                      aria-haspopup="true"
                      aria-expanded="false">
                      {this.state.name2char}
                    </span>
                    <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">

                      <h6 className="text-center">Welcome!
                        <span className="text-success ml-2">{this.state.userinfos.name}</span>
                      </h6>
                      <hr></hr>
                      {/* <a className="dropdown-item px-0" href="/"><AccountCircleIcon/>
                        My Account</a> */}
                      <NavLink className="dropdown-item px-0" to="/password-reset"><LockIcon/>
                        Password Reset</NavLink>
                      <NavLink className="dropdown-item px-0" to="/" onClick={this.logout}><ExitToAppIcon/>
                        Logout</NavLink>
                    </div>
                  </div>
                </div>

              </Toolbar>
            </AppBar>
          : (this.state.logintrue && this.state.userinfos.role==='employee')?
          <AppBar position="fixed" color="primary">
              <Toolbar>
                <Typography variant="h6" className={classes.title}>
                  <Link to="/dashboard"><img alt="default_img_react.jpg" src='/assets/logo.png' width="120"/></Link>
                </Typography>

                <div className="right-nav">                 
                  <NavLink to="/" exact>Bookings</NavLink>
                  <NavLink to="/">Orders</NavLink>
                  <NavLink to="/">Jobs</NavLink>
                  <NavLink to="/">Payments</NavLink>
                  <NavLink to="/">Invoices</NavLink>
                  <NavLink to="/">
                    <Badge color="secondary" variant="dot">
                      <NotificationsNoneIcon/>
                    </Badge>
                  </NavLink>
                  <div className="dropdown">
                    <span
                      className="dropdown-toggle text-uppercase account-dropdown"
                      id="dropdownMenuButton"
                      data-toggle="dropdown"
                      aria-haspopup="true"
                      aria-expanded="false">
                      {this.state.name2char}
                    </span>
                    <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">

                      <h6 className="text-center">Welcome!
                        <span className="text-success ml-2">{this.state.userinfos.name}</span>
                      </h6>
                      <hr></hr>
                      {/* <a className="dropdown-item px-0" href="/"><AccountCircleIcon/>
                        My Account</a> */}
                      <NavLink className="dropdown-item px-0" to="/password-reset"><LockIcon/>
                        Password Reset</NavLink>
                      <NavLink className="dropdown-item px-0" to="/" onClick={this.logout}><ExitToAppIcon/>
                        Logout</NavLink>
                    </div>
                  </div>
                </div>

              </Toolbar>
            </AppBar>
          :(this.state.logintrue && this.state.userinfos.role==='admin')?
          <AppBar position="fixed" color="primary">
              <Toolbar>
                <Typography variant="h6" className={classes.title}>
                  <Link to="/dashboard"><img alt="default_img_react.jpg" src='/assets/logo.png' width="120"/></Link>
                </Typography>

                <div className="right-nav">                 
                  <NavLink to="/admin/users" activeClassName='is-active' exact>Users</NavLink>                                
                  <NavLink to="/admin/booking" activeClassName='is-active' exact>Bookings</NavLink>
                  <NavLink to="/admin/session" activeClassName='is-active' exact>Sessions</NavLink>
                  <NavLink to="/admin/activity" activeClassName='is-active' exact>Activity</NavLink>
                  <NavLink to="/admin/external-objects" activeClassName='is-active' exact>External Objects</NavLink>
                  <NavLink to="/admin/configurations" activeClassName='is-active' exact>Configurations</NavLink>
                  <NavLink to="/" exact>
                    <Badge color="secondary" variant="dot">
                      <NotificationsNoneIcon/>
                    </Badge>
                  </NavLink>
                  <div className="dropdown">
                    <span
                      className="dropdown-toggle text-uppercase account-dropdown"
                      id="dropdownMenuButton"
                      data-toggle="dropdown"
                      aria-haspopup="true"
                      aria-expanded="false">
                      {this.state.name2char}
                    </span>
                    <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">

                      <h6 className="text-center">Welcome!
                        <span className="text-success ml-2">{this.state.userinfos.name}</span>
                      </h6>
                      <hr></hr>
                      {/* <a className="dropdown-item px-0" href="/"><AccountCircleIcon/>
                        My Account</a> */}
                      <NavLink className="dropdown-item px-0" to="/password-reset"><LockIcon/>
                        Password Reset</NavLink>
                      <NavLink className="dropdown-item px-0" to="/" onClick={this.logout}><ExitToAppIcon/>
                        Logout</NavLink>
                    </div>
                  </div>
                </div>

              </Toolbar>
            </AppBar>
            :(this.state.logintrue && this.state.userinfos.role==='owner')?
            <AppBar position="fixed" color="primary">
                <Toolbar>
                  <Typography variant="h6" className={classes.title}>
                    <Link to="/dashboard"><img alt="default_img_react.jpg" src='/assets/logo.png' width="120"/></Link>
                  </Typography>
  
                  <div className="right-nav">                 
                    <NavLink to="/" exact>Bookings</NavLink>
                    <NavLink to="/">Orders</NavLink>
                    <NavLink to="/">Jobs</NavLink>
                    <NavLink to="/">Payments</NavLink>
                    <NavLink to="/">Invoices</NavLink>
                    <NavLink to="/">Products</NavLink>
                    <NavLink to="/">Bundles</NavLink>
                    <NavLink to="/">Assets</NavLink>
                    <NavLink to="/">Location</NavLink>
                    <NavLink to="/">Employees</NavLink>
                    <NavLink to="/">Customers</NavLink>
                    <NavLink to="/">
                      <Badge color="secondary" variant="dot">
                        <NotificationsNoneIcon/>
                      </Badge>
                    </NavLink>
                    <div className="dropdown">
                      <span
                        className="dropdown-toggle text-uppercase account-dropdown"
                        id="dropdownMenuButton"
                        data-toggle="dropdown"
                        aria-haspopup="true"
                        aria-expanded="false">
                        {this.state.name2char}
                      </span>
                      <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">
  
                        <h6 className="text-center">Welcome!
                          <span className="text-success ml-2">{this.state.userinfos.name}</span>
                        </h6>
                        <hr></hr>
                        {/* <a className="dropdown-item px-0" href="/"><AccountCircleIcon/>
                          My Account</a> */}
                        <NavLink className="dropdown-item px-0" to="/password-reset"><LockIcon/>
                          Password Reset</NavLink>
                        <NavLink className="dropdown-item px-0" to="/" onClick={this.logout}><ExitToAppIcon/>
                          Logout</NavLink>
                      </div>
                    </div>
                  </div>
  
                </Toolbar>
              </AppBar>
          :<AppBar position="fixed" color="primary">
            <Toolbar>
              <Typography variant="h6" className={classes.title}>
                <Link to="/"><img alt="default_img_react.jpg" src='/assets/logo.png' width="120"/></Link>
              </Typography>

              <div className="right-nav">
                <NavLink to="/" exact>EZClean Just For You</NavLink>
                <NavLink to="/">Expert Speak</NavLink>
                <NavLink to="/">The EZClean Way</NavLink>
                <NavLink to="/contact-us">Contact Us
                </NavLink>
                <Link to="/register" className="px-0">
                  <Button
                    style={{
                    'background': '#0d6eff',
                    'color': 'white'
                  }}
                    className="mx-2">Sign Up</Button>
                </Link>
                <Link to="/login" className="px-0">
                  <Button
                    style={{
                    'background': '#0d6eff',
                    'color': 'white'
                  }}
                    className="mx-2">Sign In</Button>
                </Link>
              </div>

            </Toolbar>
          </AppBar>
          
}

      </div>

    );
  }
}

export default withRouter(Header);
