import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import {Card, CardContent, CardActions, Button, TextField} from '@material-ui/core';
import {Alert} from '@material-ui/lab';
import {Link} from 'react-router-dom';

import FormValidation from '../Validations/FormValidation';

class ResetPassword extends Component {
    constructor(props) {
        super(props);
    
        this.state = {
          newPassword: '',
          confirmPassword:'',
          formValid: false,
          error: false,
          errorText: null,
          msgShow: false
        }
      }
    
      // input validation
  validateInput = (e) => {
      console.log(FormValidation('confirmPassword', e.currentTarget.value));
    (e.target.name === 'newPassword') && (!FormValidation('newPassword', e.currentTarget.value).trueOrFalse)
      ? this.setState({
        error1: true,
        errorText1: FormValidation('newPassword', e.currentTarget.value).errorText
      })
      : this.setState({error1: false, errorText1: null});
      if(e.target.value){
        (e.target.name === 'confirmPassword') && (this.state.newPassword!==this.state.confirmPassword) && (this.state.confirmPassword!== '') ?
        this.setState({
            error2:true,
            errorText2:FormValidation('password_match', e.currentTarget.value).errorText
        }):this.setState({error2: false, errorText2: null});
      }
      else{
        (e.target.name === 'confirmPassword') && (e.target.value==='')
        ? this.setState({
          error2: true,
          errorText2: FormValidation('confirmPassword', e.currentTarget.value).errorText
        })
        : this.setState({error2: false, errorText2: null});
      }     

    (this.state.error1 === false && this.state.error2 === false) && this.setState({formValid: true});
  }

      componentDidMount = () => {
        const getHeader = document.getElementsByTagName('header');
        //hide header for
        if (window.location.pathname === '/password/reset') {
          getHeader[0]
            .classList
            .remove('transparent');
          getHeader[0]
            .classList
            .add('d-none')
        };
    
      }
      // get value of input on change 
      handleChange = (e) => {
        this.setState({
          [e.target.name]: e.currentTarget.value
        });
      }

    // submit form fn
    handleSubmit=(e)=>{
        e.preventDefault();
        if(this.state.newPassword!=='' && this.state.confirmPassword!=='' && this.state.newPassword===this.state.confirmPassword){
            this.setState({msgShow:true},()=>{
                setTimeout(()=>{this.props.history.push('/login')},3000)
            })
        }
    }
    render() {
        return (
            <React.Fragment>
                <br></br>
                <br></br>
            <div className="col-md-4 m-auto py-5 ">
              <p className="text-center"><Link to="/"><img src="/assets/logo.png" alt="logo.png" width="160"/></Link></p>
              <Card className="mt-2 py-3 px-5">
                {(this.state.msgShow)
                  ? <Alert severity="success" className="mx-3 msg">Password Reset Successfully.Redirecting to login...</Alert>
                  : null}
                
                <form
                  method="post"
                  noValidate
                  onSubmit={this.handleSubmit}
                  autoComplete="off"
                  className="w-100">
                  <CardContent>
                    <p className="text-center">
                      <strong>Reset Password</strong>
                    </p>
                    <TextField
                      id="standard-basic"
                      error={this.state.error1}
                      onBlur={this.validateInput}
                      helperText={this.state.errorText1}
                      size="small"
                      type="password"
                      name="newPassword"
                      variant="outlined"
                      label="New Password"
                      onChange={this.handleChange}
                      className="mt-3"
                      fullWidth/>
                    <TextField
                      id="standard-basic"
                      error={this.state.error2}
                      onBlur={this.validateInput}
                      helperText={this.state.errorText2}
                      size="small"
                      type="password"
                      name="confirmPassword"
                      variant="outlined"
                      label="Confirm Password"
                      onChange={this.handleChange}
                      className="mt-3"
                      fullWidth/>
                  </CardContent>
    
                  <CardActions className="d-flex flex-column px-3">
                    <Button
                      size="large"
                      type="submit"
                      variant="contained"
                      color="primary"
                      disableElevation
                      className="custom-primary btn-size-custom"
                      fullWidth>Submit</Button>
    
                  </CardActions>
                </form>
              </Card>
            </div>
          </React.Fragment>
        );
    }
}

export default withRouter(ResetPassword);
