import React, {Component, useRef} from "react";
import {ReactMUIDatatable} from "react-material-ui-datatable";
import { Button } from "@material-ui/core";
import {  Link } from 'react-router-dom';
import IconButton from "@material-ui/core/IconButton";
import SwapHorizIcon from "@material-ui/icons/SwapHoriz";
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from "@material-ui/icons/Delete";
import users from "../../session.json";
import EditSession from '../modal/EditSession';
const columns = [

    {
      name: "id",
      label: "Session ID"
    }, {
      name: "name",
      label: "User Name"
    }, {
      name: "email",
      label: "Email ID"
    }, {
      name: "sessiontime",
      label: "Session Time"
    }, {
        name: "ipaddress",
        label: "IP Address"
      }, {
      name: "status",
      label: "Status"
    }
  ];
  
  const customCell = ({value, column, row}) => {
    if (column.name === "status") {
      return value === 'suspend'
      ? <span className="text-warning">Suspend</span>:value === 'extend'?
      <span className="text-success">Extend</span>:<span className="text-danger">Block</span>;
    }
    if (column.name === "action") {
      return;
    }
    return value;
  };
  
class Session extends Component {
    constructor(props){
        super(props);
        this.state={
            open:false,
            editbooking_ref:null,            
        }
    }
    
    componentDidMount() {
        this.setState({editbooking_ref:React.createRef()})
    }
    // new booking dialog box
    openBookingDialog=()=>{        
       this.refs.child.openModal();
    }
    render() {
        
        var selectedRecord={};       
        const CustomToolbarSelectActions = ({data, selectedData, updateSelectedData, handleDelete}) => {
         
          return (      
            <React.Fragment>
              <IconButton
                onClick={() => {
                const nextSelectedData = data.reduce((nextSelectedData, row) => {
                  if (!selectedData.includes(row)) {
                    nextSelectedData.push(row);
                  }
                  return nextSelectedData;
                }, []);
                updateSelectedData(nextSelectedData);
              }}>
                <SwapHorizIcon/>
              </IconButton>
              {(selectedData.length === 1)
                ? <IconButton
                    onClick={() => {
                      selectedData.map((singleRecord, index)=>{
                        Object.assign(selectedRecord,singleRecord);
                      })
                      
                      this.state.editbooking_ref.current.openModal(); 
                  }}>
                    <EditIcon/>
                  </IconButton>
                : null
      }
      
              <IconButton onClick={() => {
                handleDelete(selectedData);
              }}>
                <DeleteIcon/>
              </IconButton>
            </React.Fragment>
          )
        };
        return (
            <div className="gap-to-header container-fluid">
                
                <ReactMUIDatatable
                    title="All Sessions"
                    data={users}
                    columns={columns}
                    customCell={customCell}
                    toolbarSelectActions={CustomToolbarSelectActions}/>
                    
                <EditSession selectedRecord={selectedRecord} ref={this.state.editbooking_ref}/>
            </div>
        );
    }
}

export default Session;
