import React, {useRef} from "react";
import {ReactMUIDatatable} from "react-material-ui-datatable";
import IconButton from "@material-ui/core/IconButton";
import SwapHorizIcon from "@material-ui/icons/SwapHoriz";
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from "@material-ui/icons/Delete";
import users from "../booking_data.json";
import EditBooking from './modal/EditBooking';
const columns = [

  {
    name: "Booking ID",
    label: "Booking ID"
  }, {
    name: "Plan Name",
    label: "Plan Name"
  }, {
    name: "Plan Amount",
    label: "Plan Amount"
  }, {
    name: "Booking Date",
    label: "Booking Date"
  }, {
    name: "status",
    label: "Status"
  }
];

const customCell = ({value, column, row}) => {
  if (column.name === "status") {
    return value === true
      ? <span className="text-success">Completed</span>
      : <span className="text-warning">Pending</span>;
  }
  if (column.name === "action") {
    return;
  }
  return value;
};

const BookingTable = (props) => {
  const editbooking_ref = useRef();
  var selectedRecord={};
  const [open,
    setOpen] = React.useState(false);
  const CustomToolbarSelectActions = ({data, selectedData, updateSelectedData, handleDelete}) => {
   
    return (

      <React.Fragment>
        <IconButton
          onClick={() => {
          const nextSelectedData = data.reduce((nextSelectedData, row) => {
            if (!selectedData.includes(row)) {
              nextSelectedData.push(row);
            }
            return nextSelectedData;
          }, []);
          updateSelectedData(nextSelectedData);
        }}>
          <SwapHorizIcon/>
        </IconButton>
        {(selectedData.length === 1)
          ? <IconButton
              onClick={() => {
                selectedData.map((singleRecord, index)=>{
                  Object.assign(selectedRecord,singleRecord);
                })
                
                
              editbooking_ref
                .current
                .openModal();
            }}>
              <EditIcon/>
            </IconButton>
          : null
}

        <IconButton onClick={() => {
          handleDelete(selectedData);
        }}>
          <DeleteIcon/>
        </IconButton>
      </React.Fragment>
    )
  };
  return (
    <div className={"App"}>

      <ReactMUIDatatable
        title="My Booking"
        data={users}
        columns={columns}
        customCell={customCell}
        toolbarSelectActions={CustomToolbarSelectActions}/>
      <EditBooking openDialog={open} selectedRecord={selectedRecord} ref={editbooking_ref}/>
    </div>
  )
};

export default BookingTable;
