import React, { Component, useRef } from 'react';
import {ReactMUIDatatable} from "react-material-ui-datatable";
import IconButton from "@material-ui/core/IconButton";
import { Button } from "@material-ui/core";
import SwapHorizIcon from "@material-ui/icons/SwapHoriz";
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from "@material-ui/icons/Delete";
import users from "../../users.json";
import EditUser from '../modal/EditUser';
const columns = [
    {
      name: "id",
      label: "ID"
    },
    {
        name:"first_name",
        label:'Name'
    },
    {
        name:"date",
        label:'Date'
    },
    {
        name:"total",
        label:'Total'
    },
    {
        name:"warehouse",
        label:'Warehouse'
    },
    {
        name:"status",
        label:'Status'
    },
  ];
const customCell = ({value, column, row}) => {
    if (column.name === "status") {
      return value === true
        ? <span className="text-success">Active</span>
        : <span className="text-danger">Block</span>;
    }
    return value;
  };
class Users extends Component {

    constructor(props){
        super(props);
        this.state={
            open:false,
            edituser_modal_ref:null
        }
    }
    componentDidMount() {
        this.setState({edituser_modal_ref:React.createRef()})
    }
    
    render() {
    
  var selectedRecord={};
  const CustomToolbarSelectActions = ({data, selectedData, updateSelectedData, handleDelete}) => {
   console.log(selectedData);
    return (
       
      <React.Fragment>
        <IconButton
          onClick={() => {
          const nextSelectedData = data.reduce((nextSelectedData, row) => {
            if (!selectedData.includes(row)) {
              nextSelectedData.push(row);
            }
            return nextSelectedData;
          }, []);
          updateSelectedData(nextSelectedData);
        }}>
          <SwapHorizIcon/>
        </IconButton>
        {(selectedData.length === 1)
          ? <IconButton
              onClick={() => {
                selectedData.map((singleRecord, index)=>{
                  Object.assign(selectedRecord,singleRecord);
                })
                this.state.edituser_modal_ref.current.openModal();                
            }}>
              <EditIcon/>
            </IconButton>
          : null
}

        <IconButton onClick={() => {
          handleDelete(selectedData);
        }}>
          <DeleteIcon/>
        </IconButton>
      </React.Fragment>
    )
  };
        return (
            <div className="gap-to-header container-fluid">
                
                <ReactMUIDatatable
        title="All Users"
        data={users}
        columns={columns}
        customCell={customCell}
        toolbarSelectActions={CustomToolbarSelectActions}/>

<EditUser openDialog={this.state.open} selectedRecord={selectedRecord} ref={this.state.edituser_modal_ref}/>
            </div>

            
        );
    }
}

export default Users;
