import React, {forwardRef, useImperativeHandle, useState, useEffect} from 'react';
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  FormControl,
  Select,
  MenuItem,
  DialogContentText,
  DialogTitle,
  TextField,
  InputLabel
} from '@material-ui/core';


import DatePicker from "react-datepicker";
import moment from 'moment';
import "react-datepicker/dist/react-datepicker.css";

const EditBooking = forwardRef((props, ref) => {
  console.log(props.selectedRecord);
  const bookingid=props.selectedRecord['Booking ID'];
  const selectedplan=props.selectedRecord['Plan Name'];
  var currentStatus = props.selectedRecord.status;
  const [status,
    setStatus] = React.useState(currentStatus);
  const [selectedStatus,
    setSelectedStatus] = React.useState(null);

  useEffect(() => {
    if (selectedStatus !== null) 
      setStatus(selectedStatus);
    else 
      setStatus(currentStatus)

  }, console.log(status))
  const  bookingDate=new Date(moment(props.selectedRecord['Booking Date']).format('MM/DD/YYYY'));
  const [open,
    setOpen] = React.useState(false);
    const [selectedDate, setSelectedDate] = React.useState(null);
    const [selectedPlan, setSelectedPlan] = React.useState(null);
  const HandleDateChange = (date) => {    
    setSelectedDate(date);
  };
  useImperativeHandle(ref, () => ({
    openModal() {
      setOpen(true);
    }
  }));

  const changePlan = (e) => {      
    setSelectedPlan(e.target.value);
    //console.log(selectedPlan);
  };
  
  const ChangeStatus = (e) => {
    if (e.target.value === 'Completed') {
      setStatus(true);
      setSelectedStatus(true)
    };
    if (e.target.value === 'Pending') {
      setStatus(false);
      setSelectedStatus(false)
    };
  };
  return (
    <div>
      <Dialog open={open} onClose={false} aria-labelledby="responsive-dialog-title">
        <DialogTitle id="responsive-dialog-title">{"Edit Booking"}</DialogTitle>
        
        <form method="post" noValidate autoComplete="off" className="w-100"><DialogContent>
          <DialogContentText>
              <TextField
                id="standard-basic"
                size="small"
                required
                name="booking_id"
                variant="outlined"
                label="Booking ID"
                value={bookingid}
                className="mt-3"
                fullWidth
                disabled/>
              <FormControl required className="mt-3"  style={{
                'width': '100%',
              
              }}>
                <InputLabel shrink className="custom-input-label" id="demo-simple-select-placeholder-label-label">
                  Select Plan
                </InputLabel>
                <Select
                  labelId="demo-simple-select-required-label"
                  id="demo-simple-select-required"
                  variant="outlined"
                  size="small"
                  value={(selectedPlan!==null)?selectedPlan:selectedplan}
                  style={{
                  'height': '40px',
                  'padding': '4px!important',
                  'width': '100%'
                }}
                onChange={changePlan}
                >

                  
                  <MenuItem value="">
                    <em>Select Plan</em>
                  </MenuItem>
                  <MenuItem value={selectedplan}>{selectedplan}</MenuItem>
                  <MenuItem value={1}>Premium(699/-)</MenuItem>
                  <MenuItem value={2}>Velvet(899/-)</MenuItem>
                </Select>
              </FormControl>

              <FormControl required className="mt-3"  style={{
                'width': '100%',
              
              }}>
                <InputLabel shrink className="custom-input-label" id="demo-simple-select-placeholder-label-label">
                 Status
                </InputLabel>
                <Select
                  labelId="demo-simple-select-required-label"
                  id="demo-simple-select-required"
                  variant="outlined"
                  size="small"
                  value={status
                  ? 'Completed'
                  : 'Pending'}
                  style={{
                  'height': '40px',
                  'padding': '4px!important',
                  'width': '100%'
                }}
                  onChange={ChangeStatus}>

                  <MenuItem value="Pending">Pending</MenuItem>
                  <MenuItem value="Completed">Completed</MenuItem>
                </Select>
              </FormControl>
              <DatePicker
              showMonthDropdown
              showYearDropdown
              className="custom-input-01"
              fullWidth   
              style={{
                'height': '40px',
                'padding': '4px!important',
                'width': '100%'
              }}       
        selected={(selectedDate!==null)?selectedDate:bookingDate}
       
        onChange={HandleDateChange}
      />
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={() => {setOpen(false);setSelectedDate(null);setStatus(currentStatus);
              setSelectedStatus(null)}} color="default btn-default">
            Cancel
          </Button>
          <Button onClick={() => {setOpen(false);setSelectedDate(null);setStatus(currentStatus);
              setSelectedStatus(null)}} color="primary" autoFocus>
            Save
          </Button>
        </DialogActions>
        
        </form>
      </Dialog>
    </div>
  );
})

export default EditBooking;
