import React, { Component } from 'react';
import {Card, CardContent, CardActions, Button, TextField} from '@material-ui/core';
import {Alert} from '@material-ui/lab';
import {Link} from 'react-router-dom';

import FormValidation from '../Validations/FormValidation';

class ForgotPassword extends Component {
    constructor(props) {
        super(props);
    
        this.state = {
          email: '',
          formValid: false,
          error: false,
          errorText: null,
          msgShow: false
        }
      }
    
      // input validation
  validateInput = (e) => {
    (e.target.name === 'email') && (!FormValidation('email', e.currentTarget.value).trueOrFalse)
      ? this.setState({
        error1: true,
        errorText1: FormValidation('email', e.currentTarget.value).errorText
      })
      : this.setState({error2: false, errorText2: null});
    (this.state.error1 === false && this.state.error2 === false) && this.setState({formValid: true});
  }

      componentDidMount = () => {
        const getHeader = document.getElementsByTagName('header');
        //hide header for
        if (window.location.pathname === '/forgot-password') {
          getHeader[0]
            .classList
            .remove('transparent');
          getHeader[0]
            .classList
            .add('d-none')
        };
    
      }
      // default fn call after rendering
    render() {
        return (
            <React.Fragment>
            <div className="col-md-4 m-auto py-4">
              <p className="text-center"><Link to="/"><img src="/assets/logo.png" alt="logo.png" width="160"/></Link></p>
              <Card className="mt-2 py-3 px-5">
                {(this.state.msgShow)
                  ? <Alert severity="error" className="mx-3 msg">Email ID or password is incorrect!</Alert>
                  : null}
                
                <form
                  method="post"
                  noValidate
                  onSubmit={this.handleSubmit}
                  autoComplete="off"
                  className="w-100">
                  <CardContent>
                    <p className="text-center">
                      <strong>Forgor Password?</strong>
                    </p>
                    <p className="text-center">Please enter you registered email id to get reset password link in your inbox to reset password.</p>
                    <TextField
                      id="standard-basic"
                      error={this.state.error1}
                      onBlur={this.validateInput}
                      helperText={this.state.errorText1}
                      size="small"
                      name="email"
                      variant="outlined"
                      label="Email ID"
                      onChange={this.handleChange}
                      className="mt-3"
                      fullWidth/>
                  </CardContent>
    
                  <CardActions className="d-flex flex-column px-3">
                    <Button
                      size="large"
                      type="submit"
                      variant="contained"
                      color="primary"
                      disableElevation
                      className="custom-primary btn-size-custom"
                      fullWidth>Sign In</Button>
                      
                <p className="text-center my-3">or</p>
                <p className="mt-2">Back to <Link to="/login">Sign In</Link></p>
    
                  </CardActions>
                </form>
              </Card>
            </div>
          </React.Fragment>
        );
    }
}

export default ForgotPassword;
