import React, { Component } from 'react';
import {Link} from 'react-router-dom';
class ThankYou extends Component {

    render() {
        return (
            <div className="card text-center gap-to-header border-0">              
              <div className="card-body my-3 py-3">
                 <img src="/assets/car2.jpg" width={300} alt="thank_you.png"/>
                 <h1 className="text-success mt-0" style={{'marginTop':'-30px'}} >Thank You!</h1>
                 <h5 className="text-black-50">You booking received successfully.To check your booking <br></br>please visit to your 
                     <Link to='/dashboard'> dashboard</Link>.</h5>
              </div>
            </div>
        );
    }
}

export default ThankYou;
