import React, {Component} from 'react';
import {NavLink} from 'react-router-dom';
import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';
import InstagramIcon from '@material-ui/icons/Instagram';
class Footer extends Component {
  constructor(props){
    super(props);
    this.state={
      userinfos:null
    }
  }
  componentWillMount=()=>{
    if(localStorage.getItem('userInfo')){
      var userinfo=JSON.parse(localStorage.getItem('userInfo'));      
      this.setState({userinfos:userinfo});
     
    }
  }
  render() {
    return (
      (this.state.userinfos && (this.state.userinfos.role==='admin' || this.state.userinfos.role==='owner'))?null:  
      <React.Fragment>
        <div className="footer-nav text-center mt-5 mb-3">
          <NavLink to="/terms-of-service">Terms of Service</NavLink>
          <NavLink to="/faq">FAQs</NavLink>
          <NavLink to="/feedback">Feedback</NavLink>
        </div>
        <div
          className="footer-nav text-center  mb-3"
          style={{
          'margin': '0 0.5rem!important'
        }}>
          <NavLink to="/"><FacebookIcon/></NavLink>
          <NavLink to="/"><TwitterIcon/></NavLink>
          <NavLink to="/"><InstagramIcon/></NavLink>
        </div>
        <p className="copyright">
           Copyright 2020. All rights reserved.</p>
      </React.Fragment>
    );
  }
}

export default Footer;
