import React, {Component} from 'react';
import Services from './Services';
import data from '../data.json';
import {TextField} from '@material-ui/core';
import users from '../userlist.json';
import Autocomplete, {createFilterOptions} from '@material-ui/lab/Autocomplete';
import {  connect } from 'react-redux';

class PickPlan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      planOpt: 2,
      plans: [],
      value: null
    }
  }
  //get selected plan
  handleOnChange = (e) => {
    let checked_plan = document
      .querySelector('input[name="plans_opt"]:checked')
      .value;
    this.setState({plans: data[0].priceFeature[checked_plan]},()=>localStorage.setItem('planamount', this.state.plans.amount));
    
  }
  componentWillMount = () => {
    this.setState({plans: data[0].priceFeature[0]},()=>
    localStorage.setItem('planamount', this.state.plans.amount));
  }

  render() {
    
    const filter = createFilterOptions();
    const plan = this.state.plans;
    return (
      <div className="card text-left shadow-none border-0">
        <div className="card-body">
          <div className="row mb-3">
            <div class="col-md-12">
            <Autocomplete
              value={this.state.value}
              onChange={(event, newValue) => {
              if (newValue && newValue.inputValue) {
                this.setState({email: newValue.inputValue});
                return;
              }
              this.setState({
                value: newValue
              }, () =>{ if(this.state.value){ this.props.setName(this.state.value.first_name);localStorage.setItem('user_autofill', JSON.stringify(this.state.value));console.log(this.pros)}});
            }}
              filterOptions={(options, params) => {
              const filtered = filter(options, params);
              if (params.inputValue !== '') {
                filtered.push({inputValue: params.inputValue, email: `Add "${params.inputValue}"`});
              }
              return filtered;
            }}
              selectOnFocus
              
              handleHomeEndKeys
              id={users.uid}
              options={users}
              getOptionLabel={(option) => {
              if (typeof option === 'string') {
                return option;
              }
              if (option.inputValue) {
                return option.inputValue;
              }
              return option.email
            }}
              renderOption={(option) => option.email}
              style={{
              width: '100%',
            }}
              freeSolo
              renderInput={(params) => (<TextField {...params} label="Customer Email ID"  variant="outlined"/>)}/>
              </div>
            </div>
          <div className="row">
            <div className="col-md-2 px-0">
              <div
                className="btn-group btn-group-toggle d-flex flex-column plans-type-groups"
                data-toggle="buttons">
                <label
                  className="btn btn-default  radius-0 radius-none active"
                  onClick={this.handleOnChange}>
                  <input
                    type="radio"
                    name="plans_opt"
                    className="plans_opt"
                    id="option0"
                    value="0"
                    autoComplete="off"
                    checked/>
                  Advanced
                </label>
                <label className="btn btn-default" onClick={this.handleOnChange}>
                  <input
                    type="radio"
                    name="plans_opt"
                    className="plans_opt"
                    id="option1"
                    value="1"
                    autoComplete="off"/>
                  Premium
                </label>
                <label className="btn btn-default" onClick={this.handleOnChange}>
                  <input
                    type="radio"
                    name="plans_opt"
                    className="plans_opt"
                    id="option2"
                    value="2"
                    autoComplete="off"/>
                  Velvet
                </label>
                <label className="btn btn-default" onClick={this.handleOnChange}>
                  <input
                    type="radio"
                    name="plans_opt"
                    className="plans_opt"
                    id="option3"
                    value="3"
                    autoComplete="off"/>
                  Maternity
                </label>
                <label className="btn btn-default" onClick={this.handleOnChange}>
                  <input
                    type="radio"
                    name="plans_opt"
                    className="plans_opt"
                    id="option4"
                    value="4"
                    autoComplete="off"/>
                  DateNight
                </label>
                <label className="btn btn-default" onClick={this.handleOnChange}>
                  <input
                    type="radio"
                    name="plans_opt"
                    className="plans_opt"
                    id="option5"
                    value="5"
                    autoComplete="off"/>
                  Refresh
                </label>
              </div>
            </div>
            <div className="col-md-7 m-auto">
              <Services
                name={plan.name}
                planamount={plan.amount + '/-'}
                imageurl={plan.imageUrl}
                features={plan.features}/>
            </div>
          </div>

        </div>
      </div>
    );
  }
}
const mapStateToProps=(state)=>{
  return{
    userName:state.userName,
    userAddress:state.userAddress
  }
}

const mapDispatchToProps=(dispatch)=>{
        return{
          setName:(userName)=>{
            dispatch({
              type:'SET_NAME',
              payload:{
                userName:userName
              }
            })
          },
          setAddress:(userAddress)=>{
            dispatch({
              type:'SET_ADDRESS',
              payload:{
                userAddress:userAddress
              }
            })
        }
      }
}
export default connect(mapStateToProps, mapDispatchToProps)(PickPlan);
