import React, {Component} from 'react';
import {Card, CardContent, CardActions, Button, TextField} from '@material-ui/core';
import {Alert} from '@material-ui/lab';
import {Link} from 'react-router-dom';
import FormValidation from '../Validations/FormValidation';
import data from '../data.json';
import {login, logout} from '../auth/Utils';

// import GetData from '../Services/GetData';
class Login extends Component {
  constructor(props) {
    super(props);

    this.state = {
      email: '',
      password: '',
      formValid: false,
      error: false,
      errorText: null,
      msgShow: false
    }
  }
  // input validation
  validateInput = (e) => {
    (e.target.name === 'email') && (!FormValidation('email', e.currentTarget.value).trueOrFalse)
      ? this.setState({
        error1: true,
        errorText1: FormValidation('email', e.currentTarget.value).errorText
      })
      : this.setState({error1: false, errorText1: null});
    (e.target.name === 'password') && (!FormValidation('password', e.currentTarget.value).trueOrFalse)
      ? this.setState({
        error2: true,
        errorText2: FormValidation('password', e.currentTarget.value).errorText
      })
      : this.setState({error2: false, errorText2: null});
    (this.state.error1 === false && this.state.error2 === false) && this.setState({formValid: true});
  }

  // assign form values to state
  handleChange = (e) => {
    this.setState({
      [e.target.name]: e.currentTarget.value
    });
  }

  // login form submit
  handleSubmit = e => {
    e.preventDefault();
    if(this.state.email===data[0].logininfo[0].username && this.state.password===data[0].logininfo[0].password){
      login();
      window.location.href = "/dashboard";
      localStorage.setItem('userInfo',JSON.stringify(data[0].logininfo[0].userinfo));

      /* code to api call of login check validation */

      // GetData('login',this.state).then((res)=>{
      //    if(this.state.email===trim(res[0].email) && this.state.password===trim(res[0].passwordhash)){
      //     login();
      //     let userinfo=[{res[0].name,res[0].email,res[0].displayname,res[0].last_login}];
      //     localStorage.setItem('userinfo',{res[]})
      //     window.location.href = "/dashboard";
      //    }
      // })

    
    }
    else if(this.state.email===data[0].logininfo[1].username && this.state.password===data[0].logininfo[1].password){
      login();
      window.location.href = "/dashboard";
      localStorage.setItem('userInfo',JSON.stringify(data[0].logininfo[1].userinfo));
    }else if(this.state.email===data[0].logininfo[2].username && this.state.password===data[0].logininfo[2].password){
      login();
      window.location.href = "/admin/users";
      localStorage.setItem('userInfo',JSON.stringify(data[0].logininfo[2].userinfo));
    }else if(this.state.email===data[0].logininfo[3].username && this.state.password===data[0].logininfo[3].password){
      login();
      window.location.href = "/dashboard";
      localStorage.setItem('userInfo',JSON.stringify(data[0].logininfo[3].userinfo));
    } else {
      logout();
      this.setState({msgShow: true});
    }
  }

  componentDidMount = () => {
    const getHeader = document.getElementsByTagName('header');
    //hide header for
    if (window.location.pathname === '/login' || window.location.pathname === '/register') {
      getHeader[0]
        .classList
        .remove('transparent');
      getHeader[0]
        .classList
        .add('d-none')
    };

  }
  // default fn call after rendering

  render() {
    return (
      <React.Fragment>
        <div className="col-md-4 m-auto py-3">
          <p className="text-center"><Link to="/"><img src="/assets/logo.png" alt="logo.png" width="160"/></Link></p>
          <Card className="mt-2 py-3 px-5">
            {(this.state.msgShow)
              ? <Alert severity="error" className="mx-3 msg">Email ID or password is incorrect!</Alert>
              : null}
            
            <form
              method="post"
              noValidate
              onSubmit={this.handleSubmit}
              autoComplete="off"
              className="w-100">
              <CardContent>
                <p className="text-center">
                  <strong>Login With EZClean</strong>
                </p>

                <TextField
                  id="standard-basic"
                  error={this.state.error1}
                  onBlur={this.validateInput}
                  helperText={this.state.errorText1}
                  size="small"
                  name="email"
                  variant="outlined"
                  label="Email ID"
                  onChange={this.handleChange}
                  className="mt-3"
                  fullWidth/>
                <TextField
                  id="standard-password-input"
                  label="Password"
                  type="password"
                  autoComplete="current-password"
                  variant="outlined"
                  size="small"
                  name="password"
                  error={this.state.error2}
                  onBlur={this.validateInput}
                  helperText={this.state.errorText2}
                  onChange={this.handleChange}
                  className="mt-3 mb-2"
                  fullWidth/>

                <Link to="/forgot-password" className="mt-3 text-right w-100 clearfix small ">Forgot Password?</Link>
              </CardContent>

              <CardActions className="d-flex flex-column px-3">
                <Button
                  size="large"
                  type="submit"
                  variant="contained"
                  color="primary"
                  disableElevation
                  className="custom-primary btn-size-custom"
                  fullWidth>Sign In</Button>
                <p className="mt-2">Don't have an account? <Link to="/register">Sign Up</Link></p>
                <p className="text-center my-3">or</p>
                <Button
                  disableElevation
                  variant="contained"
                  color="default"
                  fullWidth
                  size="large"
                  className="btn-size-custom">

                  <img src="/assets/g_ico.png" alt="googl_ico.png" className="mr-2" width="25"/>
                  Login in with Google
                </Button>

              </CardActions>
            </form>
          </Card>
        </div>
      </React.Fragment>
    );
  }
}

export default Login;
