const TOKEN_KEY = 'yes';
const COOKIE_NAME='usersession';
const now = new Date();
export const login = () => {
    let time = now.getTime();
    let expireTime = time + 2*60000*60;
    now.setTime(expireTime);
    document.cookie = COOKIE_NAME+'='+TOKEN_KEY+';expires='+now.toUTCString()+';path=/';
}

export const logout = () => {
    document.cookie = COOKIE_NAME+'=;expires='+now.toUTCString()+';path=/';
    localStorage.removeItem('userInfo');
    localStorage.removeItem('user_autofill');
    localStorage.removeItem('planamount');
}

export const isLogin = () => {
    let cookie = ("; "+document.cookie).split("; "+COOKIE_NAME+"=").pop().split(";").shift();
    if (cookie==='yes') {
        return true;
    }
    else{
        localStorage.removeItem('userInfo');
    localStorage.removeItem('user_autofill');
    localStorage.removeItem('planamount');
    return false;
    }
    
}