import React, {Component} from 'react';
import {
  Dialog,
  DialogActions,
  DialogTitle,
  FormControl,
  DialogContent,
  DialogContentText,
  TextField,
  Select,
  InputLabel,
  MenuItem,
  Button
} from '@material-ui/core';

import users from '../../userlist.json';
import Autocomplete, {createFilterOptions} from '@material-ui/lab/Autocomplete';
import DatePicker from "react-datepicker";
import moment from 'moment';
import "react-datepicker/dist/react-datepicker.css";

class NewBooking extends Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
      scroll: 'paper',
      value: null
    }
  }
  openModal = () => {
    this.setState({open: true})
  }
  componentDidMount() {
    //console.log(this.props.openDialog);
    this.setState({open: this.props.openDialog})
  }
  render() {
    const filter = createFilterOptions();
    return (
      <div>
        <Dialog
          open={this.state.open}
          onClose={false}
          scroll="body"
          aria-labelledby="responsive-dialog-title">
          <DialogTitle id="responsive-dialog-title">{"New Booking"}</DialogTitle>

          <form method="post" noValidate autoComplete="off" className="w-100">
            <DialogContent>
              <DialogContentText>
                <Autocomplete
                  value={this.state.value}
                  onChange={(event, newValue) => {
                  if (newValue && newValue.inputValue) {
                    this.setState({email: newValue.inputValue});
                    return;
                  }
                  this.setState({value: newValue});
                }}
                  filterOptions={(options, params) => {
                  const filtered = filter(options, params);
                  if (params.inputValue !== '') {
                    filtered.push({inputValue: params.inputValue, email: `Add "${params.inputValue}"`});
                  }
                  return filtered;
                }}
                  selectOnFocus
                  handleHomeEndKeys
                  id={users.uid}
                  options={users}
                  getOptionLabel={(option) => {
                  if (typeof option === 'string') {
                    return option;
                  }
                  if (option.inputValue) {
                    return option.inputValue;
                  }
                  return option.email
                }}
                  renderOption={(option) => option.email}
                  style={{
                  width: '100%'
                }}
                  freeSolo
                  renderInput={(params) => (<TextField {...params} label="Customer Email ID" variant="outlined"/>)}/>
                <FormControl
                  required
                  className="mt-3"
                  style={{
                  'width': '100%'
                }}>
                  <TextField
                    id="standard-basic"
                    size="small"
                    required
                    name="customername"
                    variant="outlined"
                    label="Customer Name"
                    value={(this.state.value !== null)
                    ? this.state.value.first_name
                    : ''}
                    className="mt-3"
                    fullWidth/>
                </FormControl>
                <FormControl
                  required
                  className="mt-3"
                  style={{
                  'width': '100%'
                }}>
                  <TextField
                    id="standard-basic"
                    size="small"
                    required
                    name="mobilenumber"
                    variant="outlined"
                    label="Mobile Number"
                    value={(this.state.value !== null)
                    ? this.state.value.mobile_number
                    : ''}
                    className="mt-3"
                    fullWidth/>
                </FormControl>
                <div className="clearfix mt-3"></div>
                <FormControl
                  required
                  className="mt-3"
                  style={{
                  'width': '100%'
                }}>
                  <InputLabel
                    className="custom-input-label"
                    id="demo-simple-select-placeholder-label-label">
                    Select Plan
                  </InputLabel>
                  <Select
                    labelId="demo-simple-select-required-label"
                    id="demo-simple-select-required"
                    variant="outlined"
                    size="small"
                    value={1}
                    style={{
                    'height': '40px',
                    'padding': '4px!important',
                    'width': '100%'
                  }}>
                    <MenuItem value={1}>Premium(699/-)</MenuItem>
                    <MenuItem value={2}>Velvet(899/-)</MenuItem>
                  </Select>
                </FormControl>
                <label className="w-100 mt-4">Booking Date
                  <DatePicker
                    showMonthDropdown
                    showYearDropdown
                    className="custom-input-01 m-0"
                    fullWidth
                    style={{
                    'height': '40px',
                    'margin-top': '-30px',
                    'padding': '4px!important',
                    'width': '100%'
                  }}/></label>
                <div className="row">
                  <div className="col-md-6">
                    <FormControl
                      required
                      className="mt-3"
                      style={{
                      'width': '100%'
                    }}>
                      <InputLabel
                        className="custom-input-label"
                        id="demo-simple-select-placeholder-label-label">
                        Select Scent
                      </InputLabel>
                      <Select
                        labelId="demo-simple-select-required-label"
                        id="demo-simple-select-required"
                        variant="outlined"
                        size="small"
                        value={1}
                        style={{
                        'height': '40px',
                        'padding': '4px!important',
                        'width': '100%'
                      }}>
                        <MenuItem value={1}>Lemon</MenuItem>
                        <MenuItem value={2}>Strawberry</MenuItem>
                      </Select>
                    </FormControl>
                  </div>
                  <div className="col-md-6">
                    <FormControl
                      required
                      className="mt-3"
                      style={{
                      'width': '100%'
                    }}>
                      <InputLabel
                        className="custom-input-label"
                        id="demo-simple-select-placeholder-label-label">
                        Vehicle Type
                      </InputLabel>
                      <Select
                        labelId="demo-simple-select-required-label"
                        id="demo-simple-select-required"
                        variant="outlined"
                        size="small"
                        value={1}
                        style={{
                        'height': '40px',
                        'padding': '4px!important',
                        'width': '100%'
                      }}>
                        <MenuItem value={1}>HatchBack</MenuItem>
                        <MenuItem value={2}>Sedan (+50/-)</MenuItem>
                        <MenuItem value={3}>SUV (+100/-)</MenuItem>
                      </Select>
                    </FormControl>
                  </div>
                </div>
                <div className="clearfix"></div>
                <div className="row mt-3">

                  <div className="col-md-12">
                    <TextField
                      id="outlined-textarea"
                      label="Address"
                      placeholder="Enter your full address"
                      multiline
                      variant="outlined"
                      size="small"
                      rows="4"
                      fullWidth
                      value={(this.state.value !== null)
                      ? this.state.value.address
                      : ''}/>
                    <div className="row">
                      <div className="col-md-3">
                        <TextField
                          id="standard-basic"
                          size="small"
                          value={(this.state.value !== null)
                          ? this.state.value.country
                          : ''}
                          name="country"
                          variant="outlined"
                          label="Country"
                          onChange=""
                          className="mt-3"/>
                      </div>
                      <div className="col-md-3">
                        <TextField
                          id="standard-basic"
                          size="small"
                          name="state"
                          value={(this.state.value !== null)
                          ? this.state.value.state
                          : ''}
                          variant="outlined"
                          label="State"
                          onChange=""
                          className="mt-3"/></div>
                      <div className="col-md-3">
                        <TextField
                          id="standard-basic"
                          size="small"
                          name="city"
                          value={(this.state.value !== null)
                          ? this.state.value.city
                          : ''}
                          variant="outlined"
                          label="City"
                          onChange=""
                          className="mt-3"/>
                      </div>
                      <div className="col-md-3">
                        <TextField
                          id="standard-basic"
                          size="small"
                          name="pincode"
                          value={(this.state.value !== null)
                          ? this.state.value.pincode
                          : ''}
                          variant="outlined"
                          label="Pin Code"
                          onChange=""
                          className="mt-3"/></div>
                    </div>
                  </div>

                </div>

              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button
                autoFocus
                onClick={() => {
                this.setState({open: false});
                this.setState({value: null})
              }}
                color="default btn-default">
                Cancel
              </Button>
              <Button
                onClick={() => {
                this.setState({open: false});
                this.setState({value: null})
              }}
                color="primary"
                autoFocus>
                Submit
              </Button>
            </DialogActions>

          </form>
        </Dialog>
      </div>
    );
  }
}

export default NewBooking;
