import React, {Component} from 'react';
import {
  Card,
  CardContent,
  CardActions,
  Button,
  TextField
} from '@material-ui/core';
import {Alert} from '@material-ui/lab';
class ContactUs extends Component {
  constructor(props) {
    super(props);
    this.state = {
      msgShow: ''
    }
  }
  render() {
    return (
      <React.Fragment>
        <div className="row container-fluid gap-to-header MuiToolbar-gutters">
          <div className="col-md-8">
            <Card className="mt-2 py-3 px-5">
              {(this.state.msgShow)
                ? <Alert severity="error" className="mx-3 msg">Email ID or password is incorrect!</Alert>
                : null}

              <form
                method="post"
                noValidate
                onSubmit={this.handleSubmit}
                autoComplete="off"
                className="w-100">
                <CardContent>
                  <h4>Contact Us</h4>
                  <p>Feel free to connect with us.</p>
                  <TextField
                    id="standard-basic"
                    error={this.state.error1}
                    onBlur={this.validateInput}
                    helperText={this.state.errorText1}
                    size="small"
                    name="name"
                    variant="outlined"
                    label="Name"
                    onChange={this.handleChange}
                    className="mt-3"
                    fullWidth/>
                  <TextField
                    id="standard-basic"
                    error={this.state.error2}
                    onBlur={this.validateInput}
                    helperText={this.state.errorText2}
                    size="small"
                    name="email"
                    variant="outlined"
                    label="Email ID"
                    onChange={this.handleChange}
                    className="mt-3"
                    fullWidth/>
                  <TextField
                    id="standard-password-input"
                    label="Mobile Number"
                    type="number"
                    autoComplete="false"
                    variant="outlined"
                    size="small"
                    name="mobileNumber"
                    error={this.state.error3}
                    onBlur={this.validateInput}
                    helperText={this.state.errorText3}
                    onChange={this.handleChange}
                    className="mt-3 mb-2"
                    fullWidth/>
                  <TextField
                    id="standard-basic"
                    label="Message"
                    multiline
                    rows={4}
                    variant="outlined"
                    className="mt-3"
                    fullWidth/>
                </CardContent>

                <CardActions className="d-flex flex-column px-3">
                  <Button
                    size="large"
                    type="submit"
                    variant="contained"
                    color="primary"
                    disableElevation
                    className="custom-primary btn-size-custom">Submit</Button>

                </CardActions>
              </form>
            </Card>
          </div>
          <div className="col-md-4">
            <Card>
              <CardContent>
                <p>
                  <strong>EZClean Pvt. Ltd.</strong>
                </p>
                <label class="mb-0 text-black-50">Email ID</label>
                <p>xyz@ezclean.com, yws.abc@ezclean.com</p>
                <label class="mb-0 text-black-50">Phone No.</label>
                <p>011-0000-00000</p>
                <label class="mb-0 text-black-50">Head Quarter.</label>
                <p>143, Street No. 11, Gafron Street, New Delhi - 110343</p>
                <div style={{
                  'width': '100%'
                }}>
                  <iframe
                    width="100%"
                    height="230"
                    src="https://maps.google.com/maps?width=100%&amp;height=230&amp;hl=en&amp;q=1%20Grafton%20Street%2C%20Dublin%2C%20Ireland+(My%20Business%20Name)&amp;ie=UTF8&amp;t=&amp;z=16&amp;iwloc=B&amp;output=embed"
                    frameborder="0"
                    scrolling="no"
                    marginheight="0"
                    marginwidth="0">
                    <a href="https://www.maps.ie/draw-radius-circle-map/">Google Maps Radius</a>
                  </iframe>
                </div>

              </CardContent>
            </Card>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default ContactUs;
