import React, { Component } from 'react';
import { Card } from '@material-ui/core';
class Configuration extends Component {
    render() {
        return (
            <div className="row container-fluid gap-to-header MuiToolbar-gutters">
          <div className="col-md-12">
            <Card className="mt-2 py-3 px-5">
                <h3>Configurations</h3>
                
                <img src="/assets/activity.svg" alt="activity.svg" className="activity-image"/>
                <p className="color-gray text-center">There is no any 'Configuration' </p>
            </Card>
            </div>
            </div>
        );
    }
}

export default Configuration;
