// import React from 'react';
import Errors from '../Validations/Errors';
const FormValidation = (type, value) => {
    if(value==='' || value===undefined || value===null){
        return {"trueOrFalse":false, "errorText":Errors.EMPTY_ERROR}
    }

    else{
        // password match  
        if(type==='password_match'){
            return {"trueOrFalse":false, "errorText":Errors.PASSWORD_MATCH};
         }
        if(type==='email'){
            if(/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(value)) return {"trueOrFalse":true};
            else return {"trueOrFalse":false, "errorText":Errors.EMAIL_ERROR};    
        }
        else{
            return {"trueOrFalse":true, "errorText":''}; 
        }

         
    }

   


}

export default FormValidation;
