import React from 'react';
const iState={
    userName:null,
    userEmail:null,
    userRole:null,
    userMobile:null,
    userAddress:null,
    userCity:null,
    userState:null,
    userPin:null,
    userCountry:null

}
const Reducer = (state=iState, action) => {
    if(action.type==="SET_NAME"){
        return action.payload;
    }
    if(action.type==="SET_ADDRESS"){
        return action.payload;
    }
    return state;
}

export default Reducer;
