import React, {Component} from 'react';
import {Button} from '@material-ui/core';
import {withRouter} from 'react-router-dom';
import CheckCircleOutlineIcon from '@material-ui/icons/CheckCircleOutline';
import {isLogin} from '../auth/Utils';
class Services extends Component {
  constructor(props){
    super(props);
    this.state={
      isLogin:false
    }
  }
  componentDidMount=()=>{
    isLogin()?this.setState({isLogin:true}):this.setState({isLogin:false})
  }
  render() {
    return (
      <React.Fragment>
        <div className="pricing-card">
          <div className="top-img-title">
            <h3 className="text-center">{this.props.name}</h3>
            <img alt="default_img_react.jpg" src={this.props.imageurl} width="260"/>
          </div>
          <div className="pricing">
            <span className="price-amount">{this.props.planamount}</span>
            <span className="duration"></span>
          </div>
          <div className="features">
            {this
              .props
              .features
              .map((feature, index) => {
                return (
                  <p key={index}><CheckCircleOutlineIcon color="primary" className="mr-2"/> {feature}</p>
                )
              })
}

          </div>
          <hr></hr>
          <p className="text-center">
            <Button
              size="large"
              variant="contained"
              color="primary"
              onClick={() => this.props.history.push('/login')} 
              className={this.state.isLogin ? 'my-4 btn-lg custom-primary d-none' : 'my-4 btn-lg custom-primary'}>Buy This Service</Button>
          </p>
        </div>
      </React.Fragment>
    );
  }
}

export default withRouter(Services);
