import React, {Component} from 'react';
import {TextField} from '@material-ui/core';
import { connect } from 'react-redux';
class Locality extends Component {
  constructor(props) {
    super(props);
    this.state = {
      error1: null,
      errorText1: null,
      vehicleType: 'hatchback',
      address:null,
    }
  }
  handleChange = (event) => {
    this.setState({scentType: event.target.value});
  };
  componentWillMount=()=>{
    var user_autofills=JSON.parse(localStorage.getItem('user_autofill'));
    this.setState({address:user_autofills.address,state:user_autofills.state,city:user_autofills.city,country:user_autofills.country,pincode:user_autofills.pincode})
  }
  render() {
    return (
      <React.Fragment>
        <div className="card text-left shadow-none border-0">

          <div className="card-body">
            <h5 className="card-title d-flex justify-content-between align-items-center">Your Locality
              <span>Price :
                <span className="text-primary"> INR {(localStorage.getItem('planamount')) && localStorage.getItem('planamount')}/-</span>
              </span>
            </h5><hr/>
            <form
              method="post"
              noValidate
              onSubmit={this.handleSubmit}
              autoComplete="off"
              className="w-100">
              <div className="row">

                <div className="col-md-10">
                <TextField
                    id="outlined-textarea"
                    label="Address"
                    placeholder="Enter your full address"
                    multiline
                    variant="outlined"
                    size="small"
                    rows="4"
                    fullWidth
                    value={this.state.address}
                    />
                  <div className="row">
                      <div className="col-md-3">
                  <TextField
                    id="standard-basic"
                    size="small"
                    value={this.state.country}
                    name="country"
                    variant="outlined"
                    label="Country"
                    onChange={this.handleChange}
                    className="mt-3"
                    />
                    </div>
                    <div className="col-md-3">
                    <TextField
                      id="standard-basic"
                      size="small"
                      name="state"
                      value={this.state.state}
                      variant="outlined"
                      label="State"
                      onChange={this.handleChange}
                      className="mt-3"
                      /></div>
                      <div className="col-md-3">
                      <TextField
                        id="standard-basic"
                        size="small"
                        name="city"
                        value={this.state.city}
                        variant="outlined"
                        label="City"
                        onChange={this.handleChange}
                        className="mt-3"
                        />  </div>
                        <div className="col-md-3">                  
                  <TextField
                    id="standard-basic"
                    size="small"
                    name="pincode"
                    value={this.state.pincode}
                    variant="outlined"
                    label="Pin Code"
                    onChange={this.handleChange}
                    className="mt-3"
                    /></div>
                    </div>
                </div>
                
              </div>
            </form>
          </div>
        </div>
      </React.Fragment>

    );
  }
}
const mapStateToProps=(state)=>{
  return{
    //userAddress:state.userAddress
  }
}

export default connect(mapStateToProps)(Locality);