import React, {Component} from 'react';
import {Card, CardContent, CardActions, Button, TextField, FormControl,  Select, MenuItem} from '@material-ui/core';
import {Alert} from '@material-ui/lab';
import {Link} from 'react-router-dom';
import FormValidation from '../Validations/FormValidation';
import data from '../data.json';
import {login, logout} from '../auth/Utils';
class Register extends Component {
  constructor(props) {
    super(props);

    this.state = {
      email: '',
      password: '',
      formValid: false,
      error: false,
      errorText: null,
      msgShow: false,
      inputProps:{autocomplete: 'new-password',
      form: {
        autocomplete: 'off',
      },}
    }
  }
  // input validation
  validateInput = (e) => {
    (e.target.name === 'email') && (!FormValidation('email', e.currentTarget.value).trueOrFalse)
      ? this.setState({
        error3: true,
        errorText3: FormValidation('email', e.currentTarget.value).errorText
      })
      : this.setState({error3: false, errorText3: null});
    (e.target.name === 'lastName') && (!FormValidation('lastName', e.currentTarget.value).trueOrFalse)
      ? this.setState({
        error2: true,
        errorText2: FormValidation('lastName', e.currentTarget.value).errorText
      })
      : this.setState({error2: false, errorText2: null});

      (e.target.name === 'firstName') && (!FormValidation('firstName', e.currentTarget.value).trueOrFalse)
      ? this.setState({
        error1: true,
        errorText1: FormValidation('firstName', e.currentTarget.value).errorText
      }): this.setState({error1: false, errorText1: null});
      (e.target.name === 'countryCode') && (!FormValidation('countryCode', e.currentTarget.value).trueOrFalse)
      ? this.setState({
        error4: true,
        errorText4: FormValidation('countryCode', e.currentTarget.value).errorText
      }): this.setState({error4: false, errorText4: null});
      (e.target.name === 'mobileNumber') && (!FormValidation('mobileNumber', e.currentTarget.value).trueOrFalse)
      ? this.setState({
        error5: true,
        errorText5: FormValidation('mobileNumber', e.currentTarget.value).errorText
      })
      : this.setState({error5: false, errorText5: null});
        (this.state.error1 === false && this.state.error2 === false && this.state.error3 === false && this.state.error4 === false  && this.state.error5 === false) && this.setState({formValid: true});
  }

  // assign form values to state
  handleChange = (e) => {
    this.setState({
      [e.target.name]: e.currentTarget.value
    });
  }

  // login form submit
  handleSubmit = e => {
    e.preventDefault();
    if (this.state.email === data[0].logininfo.username && this.state.password === data[0].logininfo.password) {
      login();
      window.location.href = "/dashboard";
    } else {
      logout();
      this.setState({msgShow: true});
    }
  }

  componentDidMount = () => {
    const getHeader = document.getElementsByTagName('header');
    //hide header for
    if (window.location.pathname === '/login' || window.location.pathname === '/register') {
      getHeader[0]
        .classList
        .remove('transparent');
      getHeader[0]
        .classList
        .add('d-none')
    };

  }
  // default fn call after rendering

  render() {
    return (
      <React.Fragment>
        <div className="col-md-4 m-auto py-3">
          <p className="text-center"><Link to="/"><img src="/assets/logo.png" alt="logo.png" width="160"/></Link></p>
          <Card className="mt-2 py-3 px-3">
            {(this.state.msgShow)
              ? <Alert severity="error" className="mx-3 msg">Email ID or password is incorrect!</Alert>
              : null}
            <form
              method="post"
              noValidate
              onSubmit={this.handleSubmit}
              autoComplete="off"
              className="w-100">
              <CardContent>
                <p className="text-center">
                  <strong>Login With EZClean</strong>
                </p>
                <div className="d-flex justify-content-between">
                <TextField
                  id="standard-basic"
                  error={this.state.error1}
                  onBlur={this.validateInput}
                  helperText={this.state.errorText1}
                  size="small"
                  name="firstName"
                  variant="outlined"
                  label="First Name"
                  onChange={this.handleChange}
                  className="mt-3 mr-1"
                  fullWidth
                  inputProps={this.state.inputProps}
                    />
                    <TextField
                  id="standard-basic"
                  error={this.state.error2}
                  onBlur={this.validateInput}
                  helperText={this.state.errorText2}
                  size="small"
                  name="lastName"
                  variant="outlined"
                  label="Last Name"
                  onChange={this.handleChange}
                  className="mt-3 ml-1"
                  fullWidth
                  inputProps={this.state.inputProps}
                    />
                    </div>
                  
                <TextField
                  id="standard-password-input"
                  label="Email ID"
                  variant="outlined"
                  size="small"
                  name="email"
                  error={this.state.error3}
                  onBlur={this.validateInput}
                  helperText={this.state.errorText3}
                  onChange={this.handleChange}
                  className="mt-3 mb-2"
                  fullWidth/>
                  <div className="d-flex justify-content-between">
                  <FormControl required>
                    <Select
                    labelId="demo-simple-select-required-label"
                    id="demo-simple-select-required"                    
                  error={this.state.error4}
                  onBlur={this.validateInput}
                  helperText={this.state.errorText4}
                    onChange={this.handleChange}
                    variant="outlined"
                    size="small"
                    autoWidth
                    className="mt-2 mb-2 mr-3"
                    style={{'height':'40px','padding':'4px!important','min-width':'60px'}}
                    >
                    <MenuItem value="">
                        <em>None</em>
                    </MenuItem>
                    <MenuItem value={10}>+91</MenuItem>
                    <MenuItem value={20}>+7234</MenuItem>
                    <MenuItem value={30}>+2</MenuItem>
                    </Select>
                </FormControl>
                  <TextField required
                  id="standard-password-input"
                  label="Mobile Number"
                  variant="outlined"
                  size="small"
                  name="mobileNumber"
                  error={this.state.error5}
                  onBlur={this.validateInput}
                  helperText={this.state.errorText5}
                  onChange={this.handleChange}
                  className="mt-2 mb-2 ml-2"
                  fullWidth/>
                  </div>
              </CardContent>

              <CardActions className="d-flex flex-column px-3">
                <Button
                  size="large"
                  type="submit"
                  variant="contained"
                  color="primary"
                  disableElevation
                  className="custom-primary btn-size-custom"
                  fullWidth>Sign Up</Button>

                <p className="mt-2">Already have an account? <Link to="/login">Sign In</Link></p>
              </CardActions>
            </form>
          </Card>
        </div>
      </React.Fragment>
    );
  }
}
export default Register;
