import React, {Component} from 'react';
import {
  Button
} from '@material-ui/core';
import {Link} from 'react-router-dom';
import BookingTable from './BookingTable';
class Dashboard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      theme: null,
      fullScreen: null,
    }
  }
// The first commit of Material-UI

setUserInfo=()=>{
  var userinfo=JSON.parse(localStorage.getItem('userInfo'));      
    this.setState({userinfos:userinfo});
}
UNSAFE_componentWillMount=()=>{
  if(localStorage.getItem('userInfo')){
    this.setUserInfo();
  }
}
 
  render() {
    return (
      <div className="gap-to-header">
        <div className="row mx-0">
          <div className="col-md-3">
            <div className="card text-center py-4">
              <div><img
                className="profile-pic img-circle"
                src={this.state.userinfos.avatar}
                alt="profile_pic.jpg"/>
              </div>
              <div className="card-body">
                <h4 className="card-title mb-0">{this.state.userinfos.name}</h4>
                <p className="card-text text-black-50">{this.state.userinfos.emailid}</p>
                <div className="grid-stats">
                  <h2 className="text-black-50">Total Booking
                    <Link to="/">{this.state.userinfos.totalbooking}</Link>
                  </h2>
                </div>
                <div className="grid-stats">
                  <h2 className="text-black-50">Pending Bookings
                    <Link to="/">{this.state.userinfos.pendingbooking}</Link>
                  </h2>
                </div>
                <div className="grid-stats">
                  <h2 className="text-black-50">My Rewards
                    <Link to="/">{this.state.userinfos.myrewards}</Link>
                  </h2>
                </div>
              </div>
            </div>
          </div>
          <div className="col-md-9">
            <div className="card text-left">
              <div className="card-body">
                <h4 className="card-title d-flex justify-content-between align-items-center">
                  <span></span>
                  <Link to="/new-booking">
                    <Button variant="contained" color="primary" disableElevation>
                      New Booking
                    </Button>
                  </Link>
                </h4>
                <BookingTable openEditDialog={this.handleClickOpen}/>
              </div>
            </div>
          </div>
        </div>

      

      </div>
    );
  }
}

export default Dashboard;
