import React, {Component} from 'react';
import Carousel from 'react-bootstrap/Carousel';
import data from '../data.json';
class ExpertSpeak extends Component {
  constructor(props){
    super(props);
    this.state={
      expertspeaks:data[0].testimonials
    }
  }
  
  render() {
    console.log(this.state.expertspeaks);
    return (
      <div>
        <div className='container col-md-7 m-auto pt-70'>
          <Carousel className="expert-carousel">
            {this.state.expertspeaks.map((expertspeak, index)=>{
                  return (<Carousel.Item>
                  <div className="expert-say-container">
                    <div className="expert-thumbs">
                      <img alt="default_img_react.jpg" src={expertspeak.userThumb}/>
                    </div>
                    <div className="expert-talk-text text-white">
                      <h4>{expertspeak.username} -
                        <span className="designation">( {expertspeak.userPost} )</span>
                      </h4>
                      <p className="fsize-20">{expertspeak.userMessage}</p>
                    </div>
                  </div>
                </Carousel.Item>)
            })}            
          </Carousel>
        </div>
      </div>
    );
  }
}

export default ExpertSpeak;
