import React, {Component} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import StarRatings from 'react-star-ratings';
import {
  List,
  ListItem,
  Divider,
  ListItemText,
  ListItemAvatar,
  Avatar,
  TextField,
  Button,
  ListItemSecondaryAction,
  Card,
  CardContent,
  CardHeader,
  CardActions
} from '@material-ui/core';

class Feedback extends Component {
  constructor(props) {
    super(props);
    this.state={
        rating:2
    }
    this.useStyles = makeStyles((theme) => ({
      root: {
        width: '100%',
        maxWidth: '36ch',
        backgroundColor: theme.palette.background.paper
      },
      inline: {
        display: 'inline'
      }
    }));
  }

  // rating star
  changeRating=( newRating, name )=>{
      let newRatings=newRating;
    this.setState({
      rating: newRatings
    });
  }
  render() {
    const classes = this.useStyles;
    return (
    <div className="row mx-0 gap-to-header">
      <div className="col-md-8">
        <Card >
          <CardContent>
            <List className={classes.root}>
              <ListItem alignItems="flex-start">
                <ListItemAvatar>
                  <Avatar src="/static/images/avatar/1.jpg" alt="avatar.png"/>
                </ListItemAvatar>
                <ListItemText
                  primary="Rish Marlon"
                  secondary={<React.Fragment>
                  {"This is an amazing service providing website."} </React.Fragment>}/>
                  <ListItemSecondaryAction>
                    <img src="https://d2sj89osparb2a.cloudfront.net/images/gecko-images/icons/5star-review.png" alt="rating.png"/> (5)
                  </ListItemSecondaryAction>
              </ListItem>
              <Divider variant="inset" component="li"/>
              <ListItem alignItems="flex-start">
                <ListItemAvatar>
                  <Avatar alt="Travis Howard" src="/static/images/avatar/2.jpg"/>
                </ListItemAvatar>
                <ListItemText
                  primary="Alex Brezz Pawn"
                  secondary={<React.Fragment>
                  {"Service and staff behavior is awesome.I am happy to taken service from EZClean."} </React.Fragment>}/>
                  <ListItemSecondaryAction>
                    <img src="https://d2sj89osparb2a.cloudfront.net/images/gecko-images/icons/5star-review.png" alt="rating.png"/> (5)
                  </ListItemSecondaryAction>
              </ListItem>
              <Divider variant="inset" component="li"/>
              <ListItem alignItems="flex-start">
                <ListItemAvatar>
                  <Avatar alt="Cindy Baker" src="/static/images/avatar/3.jpg"/>
                </ListItemAvatar>
                <ListItemText
                  primary="Richard Macmillian"
                  secondary={<React.Fragment> 
                  {'Do you have Paris recommendations? Have you ever '}</React.Fragment>}/>
                  <ListItemSecondaryAction>
                    <img src="https://d2sj89osparb2a.cloudfront.net/images/gecko-images/icons/4star-review.png" alt="rating.png"/> (4)
                  </ListItemSecondaryAction>
              </ListItem>
              
              <Divider variant="inset" component="li"/>
              <ListItem alignItems="flex-start">
                <ListItemAvatar>
                  <Avatar alt="Cindy Baker" src="/static/images/avatar/4.jpg"/>
                </ListItemAvatar>
                <ListItemText
                  primary="Michael Bewan"
                  secondary={<React.Fragment> 
                  {'Do you have Paris recommendations? Have you ever '}</React.Fragment>}/>
                  <ListItemSecondaryAction>
                    <img src="https://d2sj89osparb2a.cloudfront.net/images/gecko-images/icons/4star-review.png" alt="rating.png"/> (4)
                  </ListItemSecondaryAction>
              </ListItem>
              <Divider variant="inset" component="li"/>
              <ListItem alignItems="flex-start">
                <ListItemAvatar>
                  <Avatar alt="Cindy Baker" src="/static/images/avatar/4.jpg"/>
                </ListItemAvatar>
                <ListItemText
                  primary="Carl Hooper"
                  secondary={<React.Fragment> 
                  {'Awesome, fabulous '}</React.Fragment>}/>
                  <ListItemSecondaryAction>
                    <img src="https://d2sj89osparb2a.cloudfront.net/images/gecko-images/icons/5star-review.png" alt="rating.png"/> (5)
                  </ListItemSecondaryAction>
              </ListItem>
            </List>
          </CardContent>
        </Card>
      </div>
      <div className="col-md-4">
            <Card className="p-4">
               
            <form id="rate-us-frm">
                <CardHeader title="Give Your Feedback"/>
                <CardContent>
                <StarRatings
          rating={this.state.rating}
          starDimension="35px"
          starRatedColor="orange"
          starSpacing="5px"
          changeRating={this.changeRating}
          numberOfStars={5}
          name='rating'
        />
                <TextField
                    id="standard-basic"
                    label="Message"
                    multiline
                    rows={4}
                    variant="outlined"
                    className="mt-4"
                    fullWidth/>
                </CardContent>

                <CardActions className=" px-3">
                  <Button
                    size="large"
                    type="submit"
                    variant="contained"
                    color="primary"
                    disableElevation
                    className="custom-primary btn-size-custom">Submit</Button>

                </CardActions>
            </form>
            </Card>
      </div>
      </div>
    );
  }
}

export default Feedback;
