import React, {Component} from 'react';
import {TextField, FormControlLabel, FormControl, Radio, RadioGroup} from '@material-ui/core';
class PersonalInfo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      error1: null,
      errorText1: null,
      vehicleType: 'hatchback'
    }
  }
  handleChange = (event) => {
    this.setState({scentType: event.target.value});
  };

  componentWillMount=()=>{
    var user_autofills=JSON.parse(localStorage.getItem('user_autofill'));
    this.setState({name:user_autofills.first_name+' '+ user_autofills.last_name,email:user_autofills.email,mobile_number:user_autofills.mobile_number})
  }
  render() {
    return (
      <React.Fragment>
        <div className="card text-left shadow-none border-0">

          <div className="card-body">
            <h5 className="card-title d-flex justify-content-between align-items-center">Enter Your Details
              <span>Price :
                <span className="text-primary"> INR {(localStorage.getItem('planamount')) && localStorage.getItem('planamount')}/-</span>
              </span>
            </h5><hr/>
            <form
              method="post"
              noValidate
              onSubmit={this.handleSubmit}
              autoComplete="off"
              className="w-100">
              <div className="row">

                <div className="col-md-6">
                  <TextField
                    id="standard-basic"
                    error={this.state.error1}
                    onBlur={this.validateInput}
                    helperText={this.state.errorText1}
                    size="small"
                    name="name"
                    variant="outlined"
                    label="Name"
                    value={this.state.name}
                    onChange={this.handleChange}
                    className="mt-3"
                    fullWidth/>
                  <TextField
                    id="standard-basic"
                    error={this.state.error1}
                    onBlur={this.validateInput}
                    helperText={this.state.errorText1}
                    value={this.state.email}
                    size="small"
                    name="email"
                    variant="outlined"
                    label="Email ID"
                    onChange={this.handleChange}
                    className="mt-3"
                    fullWidth/>

                  <TextField
                    id="standard-basic"
                    error={this.state.error1}
                    onBlur={this.validateInput}
                    helperText={this.state.errorText1}
                    size="small"
                    name="mobileNumber"
                    value={this.state.mobile_number}
                    variant="outlined"
                    label="Mobile Number"
                    onChange={this.handleChange}
                    className="mt-3 mb-3"
                    fullWidth/>
                </div>
                <div className="col-md-6">
                  <p>Vehicle Type</p>
                  <div className="border pt-2 px-4">
                    <FormControl component="fieldset">
                      <RadioGroup
                        aria-label="gender"
                        name="gender1"
                        value={this.state.vehicleType}
                        onChange={this.handleChange}>
                        <FormControlLabel
                          value="hatchback"
                          control={< Radio color = "primary" />}
                          checked
                          label="Hatchback"/>
                        <FormControlLabel
                          value="sedan"
                          control={< Radio color = "primary" />}
                          label="Sedan (+50/-)"/>
                        <FormControlLabel
                          value="SUV"
                          control={< Radio color = "primary" />}
                          label="SUV (+100/-)"/>
                      </RadioGroup>
                    </FormControl>
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>
      </React.Fragment>

    );
  }
}

export default PersonalInfo;