import React, { Component } from 'react';
import CheckCircleOutlineIcon from '@material-ui/icons/CheckCircleOutline';
import {FormControl, FormControlLabel, RadioGroup, Radio} from '@material-ui/core';
class PersonalizePlan extends Component {
    constructor(props){
        super(props);
        this.state={
            scentType:'lemon'
        }
    }
    handleChange = (event) => {
        this.setState({scentType:event.target.value});
      };
    render() {
        return (
            <React.Fragment>
                <div className="card text-left shadow-none border-0">
                 
                  <div className="card-body">
                        <h5 className="card-title d-flex justify-content-between align-items-center">Personalize Valvet <span>Price : <span className="text-primary">INR {(localStorage.getItem('planamount')) && localStorage.getItem('planamount')}/-</span></span></h5><hr/>
                        <div className="row">
                            <ul className="list-unstyled col-4">
                                <li className="media">                        
                                    <div className="media-body">
                                        <p className="d-flex justify-content-between align-items-center">Advanced Carwash <CheckCircleOutlineIcon className="text-success"/></p>
                                    </div>  
                                </li>
                                <li className="media">                        
                                    <div className="media-body">
                                        <p className="d-flex justify-content-between align-items-center">Deep Vacuum <CheckCircleOutlineIcon className="text-success"/></p>
                                    </div>  
                                </li>
                                <li className="media">                        
                                    <div className="media-body">
                                        <p className="d-flex justify-content-between align-items-center">Air Sanitization <CheckCircleOutlineIcon className="text-success"/></p>
                                    </div>  
                                </li>
                                <li className="media">                        
                                    <div className="media-body">
                                        <p className="d-flex justify-content-between align-items-center">Wheel Polish <CheckCircleOutlineIcon className="text-success"/></p>
                                    </div>  
                                </li>
                                <li className="media">                        
                                    <div className="media-body">
                                        <p className="d-flex justify-content-between align-items-center">Advanced Carwash <CheckCircleOutlineIcon className="text-success"/></p>
                                    </div>  
                                </li>
                                <li className="media">                        
                                    <div className="media-body">
                                        <p className="d-flex justify-content-between align-items-center">Advanced Carwash <CheckCircleOutlineIcon className="text-success"/></p>
                                    </div>  
                                </li>
                            </ul>
                            <div className="col-8">
                                <p>Scent (Choose One)</p>
                                <div className="border p-4">
                                    <FormControl component="fieldset">
                                        <RadioGroup aria-label="gender" name="gender1" value={this.state.scentType} onChange={this.handleChange}>
                                            <FormControlLabel value="lemon" control={<Radio color="primary" />} label="Lemmon" />
                                            <FormControlLabel value="strawberry" control={<Radio color="primary"/>} label="Strawberry" />
                                        </RadioGroup>
                                    </FormControl>
                                </div>
                            </div>
                        </div>
                  </div>
                </div>
                
            </React.Fragment>
        );
    }
}

export default PersonalizePlan;
