import React, { Component } from 'react';
import Header from './layouts/Header';
import Footer from './layouts/Footer';
import {BrowserRouter as Routers,Switch} from 'react-router-dom';
import Home from './components/Home';
import Login from './components/Login';
import Register from './components/Register';
import Dashboard from './components/Dashboard';
import PrivateRoute from './auth/PrivateRoute';
import PublicRoute from './auth/PublicRoute';
import ThankYou from './components/ThankYou';
import NewBooking from './components/NewBooking';
import ForgotPassword from './components/ForgotPassword';
import ResetPassword from './components/ResetPassword';
import ContactUs from './components/ContactUs';
import Faq from './components/Faq';
import TermsOfServices from './components/TermsOfServices';
import Feedback from './components/Feedback';
import AdminUsers from './components/admin/Users';
import AdminBooking from './components/admin/Booking';
import AdminSession from './components/admin/Session';
import AdminActivity from './components/admin/Activity';
import AdminExternalObj from './components/admin/ExternalObjects';
import AdminConfigurations from './components/admin/Configuration';
// import PersonalizePlan from './components/PersonalizePlan';

class Routes extends Component {
  
    render() {
    
        return (
            <Routers>
                 <Header/>
                <Switch>
                    {/* public routes */}
                    <PublicRoute restricted={true}  component={Home} path="/" exact  />
                    <PublicRoute restricted={true} component={Login} path="/login" exact />
                    <PublicRoute restricted={true} component={Register} path="/register" exact />
                    <PublicRoute restricted={true} component={ForgotPassword} path="/forgot-password" exact />
                    <PublicRoute restricted={true} component={ResetPassword} path="/password/reset" exact />
                    <PublicRoute restricted={true} component={ContactUs} path="/contact-us" exact />
                    <PublicRoute restricted={true} component={Faq} path="/faq" exact />
                    <PublicRoute restricted={true} component={TermsOfServices} path="/terms-of-service" exact />
                    <PublicRoute restricted={true} component={Feedback} path="/feedback" exact />


                    {/* private routes */}
                    <PrivateRoute exact path="/dashboard" component={Dashboard} />
                    <PrivateRoute exact path="/thank-you" component={ThankYou} />
                    <PrivateRoute exact path="/new-booking" component={NewBooking} />
                    <PrivateRoute exact path="/password-reset" component={ResetPassword} />
                    <PrivateRoute exact path="/admin/users" component={AdminUsers} />
                    <PrivateRoute exact path="/admin/booking" component={AdminBooking} />
                    <PrivateRoute exact path="/admin/session" component={AdminSession} />
                    <PrivateRoute exact path="/admin/activity" component={AdminActivity} />
                    <PrivateRoute exact path="/admin/external-objects" component={AdminExternalObj} />
                    <PrivateRoute exact path="/admin/configurations" component={AdminConfigurations} />
                </Switch>
                <Footer/>
            </Routers>
        );
    }
}

export default Routes;

