import React, {Component, useRef} from "react";
import {ReactMUIDatatable} from "react-material-ui-datatable";
import { Button } from "@material-ui/core";
import {  Link } from 'react-router-dom';
import IconButton from "@material-ui/core/IconButton";
import SwapHorizIcon from "@material-ui/icons/SwapHoriz";
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from "@material-ui/icons/Delete";
import users from "../../booking_data.json";
import NewBooking from '../modal/NewBooking';
import EditBooking from '../modal/EditBooking';
const columns = [

    {
      name: "Booking ID",
      label: "Booking ID"
    }, {
      name: "Plan Name",
      label: "Plan Name"
    }, {
      name: "Plan Amount",
      label: "Plan Amount"
    }, {
      name: "Booking Date",
      label: "Booking Date"
    }, {
      name: "status",
      label: "Status"
    }
  ];
  
  const customCell = ({value, column, row}) => {
    if (column.name === "status") {
      return value === true
      ? <span className="text-success">Completed</span>
      : <span className="text-warning">Pending</span>;
    }
    if (column.name === "action") {
      return;
    }
    return value;
  };
  
class Booking extends Component {
    constructor(props){
        super(props);
        this.state={
            open:false,
            editbooking_ref:null,            
        }
    }
    
    componentDidMount() {
        this.setState({editbooking_ref:React.createRef()})
    }
    // new booking dialog box
    openBookingDialog=()=>{        
       this.refs.child.openModal();
    }
    render() {
        
        var selectedRecord={};       
        const CustomToolbarSelectActions = ({data, selectedData, updateSelectedData, handleDelete}) => {
         
          return (      
            <React.Fragment>
              <IconButton
                onClick={() => {
                const nextSelectedData = data.reduce((nextSelectedData, row) => {
                  if (!selectedData.includes(row)) {
                    nextSelectedData.push(row);
                  }
                  return nextSelectedData;
                }, []);
                updateSelectedData(nextSelectedData);
              }}>
                <SwapHorizIcon/>
              </IconButton>
              {(selectedData.length === 1)
                ? <IconButton
                    onClick={() => {
                      selectedData.map((singleRecord, index)=>{
                        Object.assign(selectedRecord,singleRecord);
                      })
                      
                      this.state.editbooking_ref.current.openModal(); 
                  }}>
                    <EditIcon/>
                  </IconButton>
                : null
      }
      
              <IconButton onClick={() => {
                handleDelete(selectedData);
              }}>
                <DeleteIcon/>
              </IconButton>
            </React.Fragment>
          )
        };
        return (
            <div className="gap-to-header container-fluid">
                
                    <Button variant="contained" color="primary" onClick={this.openBookingDialog} className="float-right mb-3" disableElevation>
                      New Booking
                    </Button>
                <div class="clearfix"></div>
                <ReactMUIDatatable
                    title="All Bookings"
                    data={users}
                    columns={columns}
                    customCell={customCell}
                    toolbarSelectActions={CustomToolbarSelectActions}/>
                    
                <EditBooking selectedRecord={selectedRecord} ref={this.state.editbooking_ref}/>
                <NewBooking ref="child" />
            </div>
        );
    }
}

export default Booking;
