import React, {forwardRef, useImperativeHandle, useState, useEffect} from 'react';
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  FormControl,
  Select,
  MenuItem,
  DialogContentText,
  DialogTitle,
  TextField,
  InputLabel
} from '@material-ui/core';

const EditUser = forwardRef((props, ref) => {
  const [open,
    setOpen] = React.useState(false);
  var currentStatus = props.selectedRecord.status;
  const [status,
    setStatus] = React.useState(currentStatus);
  const [selectedStatus,
    setSelectedStatus] = React.useState(null);

  useEffect(() => {
    if (selectedStatus !== null) 
      setStatus(selectedStatus);
    else 
      setStatus(currentStatus)

  }, console.log(status))

  const ChangeStatus = (e) => {
    if (e.target.value === 'Active') {
      setStatus(true);
      setSelectedStatus(true)
    };
    if (e.target.value === 'Block') {
      setStatus(false);
      setSelectedStatus(false)
    };
  };
  useImperativeHandle(ref, () => ({
    openModal() {
      setOpen(true);
    }
  }));
  return (
    <div>
      <Dialog open={open} onClose={false} aria-labelledby="responsive-dialog-title">
        <DialogTitle id="responsive-dialog-title">{"Edit User"}</DialogTitle>

        <form method="post" noValidate autoComplete="off" className="w-100">
          <DialogContent>
            <DialogContentText>

              <FormControl
                required
                className="mt-3"
                style={{
                'width': '100%'
              }}>
                <InputLabel
                  shrink
                  className="custom-input-label"
                  id="demo-simple-select-placeholder-label-label">
                  User Status
                </InputLabel>
                <Select
                  labelId="demo-simple-select-required-label"
                  id="demo-simple-select-required"
                  variant="outlined"
                  size="small"
                  value={status
                  ? 'Active'
                  : 'Block'}
                  style={{
                  'height': '40px',
                  'padding': '4px!important',
                  'width': '100%'
                }}
                  onChange={ChangeStatus}>

                  <MenuItem value="Active">Active</MenuItem>
                  <MenuItem value="Block">Block</MenuItem>
                </Select>
              </FormControl>

            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button
              autoFocus
              onClick={() => {
              setOpen(false);
              setStatus(currentStatus);
              setSelectedStatus(null)
            }}
              color="default btn-default">
              Cancel
            </Button>
            <Button
              onClick={() => {
              setOpen(false);
              setStatus(currentStatus);
              setSelectedStatus(null)
            }}
              color="primary"
              autoFocus>
              Save
            </Button>
          </DialogActions>

        </form>
      </Dialog>
    </div>
  );
})

export default EditUser;
