import React, {Component} from 'react';
import ExpertSpeak from './ExpertSpeak';
import Services from './Services';
import data from '../data.json';

class Home extends Component {
  constructor(props) {
    super(props);
    this.state={
      plans:data[0].priceFeature
    }
  }
  componentDidMount = () => {
   
    const getHeader = document.getElementsByTagName('header');

    //stick header  background color
    getHeader[0]
      .classList
      .remove('d-none');
    getHeader[0]
      .classList
      .add('transparent')

  }

  render() {
    return (
      <React.Fragment>
        <div className="expert-speak-container">
          <ExpertSpeak/>
        </div>
        <br/>
        <h3 className="text-center">Pick Your EZClean Service</h3>
        <hr className="ruler-hr"/>
        <div className="service-container">
          {
            this.state.plans.map((plan,index)=>{
                return(
                  <Services
                    name={plan.name}
                    planamount={plan.amount+'/-'}
                    imageurl={plan.imageUrl}
                    features={plan.features}/>
                )
            })
          }
          
        </div>

      </React.Fragment>
    );
  }
}

export default Home;
